import CloseIcon from "assets/img/Dialog/close_icon.svg";
import DocIcon from "assets/img/Dialog/doc_icon.svg";
import LeftIcon from "assets/img/Dialog/left_icon.svg";
import RightIcon from "assets/img/Dialog/right_icon.svg";
import DownloadIcon from "assets/img/buttonIcons/Download_Icon.svg";
import RotateIcon from "assets/img/fileUploadIcons/Rotate_Image.svg";
import ZoomInIcon from "assets/img/fileUploadIcons/Zoom_In.svg";
import ZoomOutIcon from "assets/img/fileUploadIcons/Zoom_Out.svg";
import axios from "axios";
import AudioPlayer from "components/AudioPlayer/AudioPlayer";
import DialogBox from "components/Dialog/Dialog";
import PdfCustom from "components/PdfCustom/PdfCustom";
import SkeletonCustom from "components/Skeleton/Skeleton";
import React, { Fragment, ReactNode, useEffect, useRef, useState } from "react";
import FileUploadService from "services/FileUploadService";

interface IMediaGalleryProps {
  visible: boolean;
  onToggleVisibility: () => void;
  list: IMediaInfo[];
  currentSection?: IMediaInfo;
}

export interface IMediaInfo {
  uid: string;
  placeOfUpload: string;
  name: string;
  fileName: string;
  category: string;
  type: string;
  sectionName: string;
  fileType: string;
  download: string;
  url: string | Promise<string>;
}

const FILE_TYPES = {
  IMAGE: "image",
  VIDEO: "video",
  PDF: "pdf",
  AUDIO: "audio",
};

interface IImagePreview {
  src: string;
  alt: string;
}

export const initialMediaGallerySectionInfo: IMediaInfo = {
  uid: "",
  placeOfUpload: "",
  name: "",
  fileName: "",
  category: "",
  type: "",
  sectionName: "",
  fileType: "",
  download: "",
  url: "",
};

export const PLACE_OF_UPLOAD_KYC = {
  LOGIN_QDE: "Login - QDE",
  LOGIN_APPLICANT: "Login - Applicant",
  LOGIN_COAPPLICANTS: "Login - Co-Applicants",
  LOGIN_GUARANTORS: "Login - Guarantors",
  LOGIN_CORPORATE_COAPPLICANT: "Login - Corporate Co-Applicants",
  LOGIN_CORPORATE_GUARANTORS: "Login - Corporate Guarantors",
  LOGIN_DEPENDANTS: "Login - Dependants",
};

const ImagePreview = (props: IImagePreview) => {
  const { src, alt } = props;
  const [rotate, setRotate] = useState(0);
  const [scale, setScale] = useState(1);

  const onImageRotate = () => {
    setRotate((prev) => {
      if (prev <= -270) {
        return 0;
      }
      return prev - 90;
    });
  };

  return (
    <div className="flex flex-col h-full justify-between">
      <div className="max-h-[380px] overflow-hidden">
        <img
          src={src}
          alt={alt}
          className="object-fill"
          style={{
            width: "100%",
            height: "100%",
            transform: `scale(${scale}) rotate(${rotate}deg)`,
            objectFit: "contain",
          }}
        />
      </div>
      <div className="w-full self-end mt-1 flex justify-start items-center gap-4 z-40">
        <button className="cursor-pointer" onClick={onImageRotate}>
          <img src={RotateIcon} alt="rotate" />
        </button>
        <div className="h-6 right-control rounded-lg flex gap-1 px-2 bg-secondary-steelBlue-1000">
          <button
            className="w-3 self-center zoom-out-control cursor-pointer"
            onClick={() => {
              setScale((prev) => {
                if (prev >= 1.1) {
                  return Math.round((prev - 0.1) * 10) / 10;
                } else return 1;
              });
            }}
          >
            <img src={ZoomOutIcon} alt="zoomOut" />
          </button>

          <input
            className="cropper-zoom-range"
            type="range"
            value={scale}
            min={1}
            max={3}
            step={0.1}
            aria-labelledby="Zoom"
            onChange={(e: React.BaseSyntheticEvent) => setScale(Number(e.target.value))}
          />

          <button
            className="w-3 self-center zoom-in-control cursor-pointer"
            onClick={() => {
              setScale((prev) => {
                if (prev <= 2.9) {
                  return Math.round((prev + 0.1) * 10) / 10;
                } else return 3;
              });
            }}
          >
            <img src={ZoomInIcon} alt="zoomIn" />
          </button>
        </div>
      </div>
    </div>
  );
};

const MediaGallery: React.FC<IMediaGalleryProps> = (props: IMediaGalleryProps) => {
  const { visible, list, currentSection, onToggleVisibility } = props;

  /*---------------------------------------States------------------------------------------ */

  const [currentSectionInfo, setCurrentSectionInfo] = useState<IMediaInfo>(
    initialMediaGallerySectionInfo
  );
  const [signedURL, setSignedURL] = useState("");
  const [currentSectionIndex, setCurrentSectionIndex] = useState<number | null>(null);
  const [showLoader, setShowLoader] = useState(false);

  /*--------------------refs--------------------- */
  const videoPlayerRef = useRef<any>(null);

  /*--------------------------------------useEffect-------------------------------------- */

  useEffect(() => {
    console.log("Media Gallery mounted");
  }, []);

  useEffect(() => {
    if (currentSection) {
      setCurrentSectionInfo(currentSection);
    }
  }, [currentSection]);

  useEffect(() => {
    const getSignedUrlFromUnsigned = async () => {
      if (currentSectionInfo.url) {
        setShowLoader(true);
        getSignedURL(await currentSectionInfo.url)
          .then((url) => {
            setSignedURL(url);
            setShowLoader(false);
          })
          .catch((err) => {
            console.log(err);
            setShowLoader(false);
          });
      }
    };
    getSignedUrlFromUnsigned();
  }, [currentSectionInfo]);

  useEffect(() => {
    getActiveIndex();
  }, [list]);

  /* ----------------------Event handlers : onClick, onToggle etc-------------------------- */

  const onLeftClick = () => {
    console.log("left click");
    const index = Number(currentSectionIndex) - 1;
    setCurrentSectionIndex(index);
    setCurrentSectionInfo(list[index]);
  };

  const onRightClick = () => {
    console.log("right click");
    const index = Number(currentSectionIndex) + 1;
    setCurrentSectionIndex(index);
    setCurrentSectionInfo(list[index]);
  };

  const onDownloadMedia = async () => {
    const signedURL = await getSignedURL(await currentSectionInfo.url);

    const downloadLink = document.createElement("a");
    downloadLink.href = signedURL;
    downloadLink.download = currentSectionInfo.fileName;

    if (currentSectionInfo?.fileType?.includes(FILE_TYPES.VIDEO)) {
      const response = await axios.get(signedURL, { responseType: "blob" });
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", `file-vid-${Math.random().toString(36).substring(2)}.mp4`);
      document.body.appendChild(link);
      link.click();
      document.parentNode?.removeChild(link);
      URL.revokeObjectURL(url);
      return;
    } else if (currentSectionInfo?.fileType?.includes(FILE_TYPES.AUDIO)) {
      const response = await axios.get(signedURL, { responseType: "blob" });
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", `file-aud-${Math.random().toString(36).substring(2)}.mp3`);
      document.body.appendChild(link);
      link.click();
      document.parentNode?.removeChild(link);
      URL.revokeObjectURL(url);
      return;
    } else if (
      currentSectionInfo?.fileType?.includes(FILE_TYPES.PDF) ||
      currentSectionInfo?.fileType?.includes(FILE_TYPES.IMAGE)
    ) {
      downloadLink.target = "_blank";
      downloadLink.rel = "noreferrer noopener";
    }

    document.body.appendChild(downloadLink);
    downloadLink.click();
    downloadLink.parentNode?.removeChild(downloadLink);
    URL.revokeObjectURL(signedURL);
  };

  /*---------------------------------Helper Functions-------------------------------------- */

  const getActiveIndex = () => {
    if (list.length > 0) {
      const findIndex = list.findIndex((item) => item.uid === currentSectionInfo.uid);
      if (findIndex > -1) {
        setCurrentSectionIndex(findIndex);
      }
    }
  };

  const getSignedURL = async (url: string) => {
    try {
      const res = FileUploadService.getSignedURL(url);
      return (await res).data.SignedUrl;
    } catch (err) {
      console.log(err);
    }
  };

  /*---------------------------------Sectional Rendering -------------------------------------- */

  const renderPdfPreview = (): ReactNode => {
    return (
      <Fragment>
        <div className="w-full h-[400px]">
          <PdfCustom source={signedURL} />
        </div>
      </Fragment>
    );
  };

  const renderVideoPreview = (): ReactNode => {
    return (
      <div className="w-full">
        <video
          ref={videoPlayerRef}
          className="w-full h-[400px] max-h-[450px] rounded-lg"
          src={signedURL}
          controls
          controlsList="nodownload"
          disablePictureInPicture
        />
      </div>
    );
  };

  const renderAudioPreview = (): ReactNode => {
    return (
      <div className="w-full h-[400px]">
        <div className="w-full h-full">
          <AudioPlayer className={"rounded-lg"} mediaSource={signedURL} />
        </div>
      </div>
    );
  };

  const renderMedia = () => {
    const { fileType } = currentSectionInfo;
    if (showLoader) {
      return (
        <div className="flex flex-col gap-6 h-full justify-center items-center bg-primary-sapphireBlue-100">
          <SkeletonCustom borderRadius="8px" height="436px" />
        </div>
      );
    }

    if (fileType?.includes(FILE_TYPES.IMAGE)) {
      return <ImagePreview src={signedURL} alt={currentSectionInfo.fileName} />;
    } else if (fileType?.includes(FILE_TYPES.VIDEO)) {
      return renderVideoPreview();
    } else if (fileType?.includes(FILE_TYPES.AUDIO)) {
      return renderAudioPreview();
    } else if (fileType?.includes(FILE_TYPES.PDF)) {
      return renderPdfPreview();
    } else {
      return (
        <div className="flex flex-col gap-6 h-full justify-center items-center bg-primary-sapphireBlue-100">
          <img src={DocIcon} />
          <a
            className="text-xs font-normal underline text-secondary-steelBlue-500"
            href={signedURL}
            target="_blank"
          >
            Click to open in a new tab
          </a>
        </div>
      );
    }
  };

  /*--------------------------Main Render-------------------------- */
  return (
    <div className="media-gallery-container">
      <DialogBox
        className={`dialog-media-gallery h-[526px] rounded-xl`}
        visible={visible}
        closable={false}
        style={{ width: "70vw" }}
      >
        <div className="flex flex-col w-full h-full gap-2.5">
          <div className="flex justify-end">
            <button onClick={onToggleVisibility}>
              <img src={CloseIcon} />
            </button>
          </div>

          <div className="flex-grow w-full flex flex-row gap-4 ">
            <div className="w-3/5 rounded-lg">{renderMedia()}</div>

            <div className="w-2/5 flex flex-col gap-5">
              <div className="flex flex-row w-full ">
                <div className="w-1/2 text-secondary-steelBlue-500 font-normal text-xs">
                  Place of upload :
                </div>
                <div
                  className="w-1/2 text-xs text-black font-normal"
                  title={currentSectionInfo.placeOfUpload ?? "-"}
                >
                  {currentSectionInfo.placeOfUpload ?? "-"}
                </div>
              </div>

              <div className="flex flex-row w-full ">
                <div className="w-1/2 text-secondary-steelBlue-500 font-normal text-xs">Name :</div>
                <div
                  className="w-1/2 text-xs text-black font-normal"
                  title={currentSectionInfo.name ?? "-"}
                >
                  {currentSectionInfo.name ?? "-"}
                </div>
              </div>

              <div className="flex flex-row w-full">
                <div className="w-1/2 text-secondary-steelBlue-500 font-normal text-xs">
                  File Name :
                </div>
                <div
                  className="w-1/2 text-xs text-black font-normal"
                  title={currentSectionInfo.fileName ?? "-"}
                >
                  {currentSectionInfo.fileName ?? "-"}
                </div>
              </div>

              <div className="flex flex-row w-full ">
                <div
                  className="w-1/2 text-secondary-steelBlue-500 font-normal text-xs"
                  title={currentSectionInfo.category ?? "-"}
                >
                  Category :
                </div>
                <div
                  className="w-1/2 text-xs text-black font-normal"
                  title={currentSectionInfo.category ?? "-"}
                >
                  {currentSectionInfo.category ?? "-"}
                </div>
              </div>

              <div className="flex flex-row w-full ">
                <div className="w-1/2 text-secondary-steelBlue-500 font-normal text-xs">Type :</div>
                <div
                  className="w-1/2 text-xs text-black font-normal"
                  title={currentSectionInfo.type ?? "-"}
                >
                  {currentSectionInfo.type ?? "-"}
                </div>
              </div>

              <div className="flex flex-row w-full ">
                <div className="w-1/2 text-secondary-steelBlue-500 font-normal text-xs">
                  Section :
                </div>
                <div
                  className="w-1/2 text-xs text-black font-normal"
                  title={currentSectionInfo.sectionName ?? "-"}
                >
                  {currentSectionInfo.sectionName ?? "-"}
                </div>
              </div>

              <div className="flex flex-row w-full ">
                <div className="w-1/2 text-secondary-steelBlue-500 font-normal text-xs">
                  File Type :
                </div>
                <div
                  className="w-1/2 text-xs text-black font-normal"
                  title={currentSectionInfo.fileType}
                >
                  {currentSectionInfo.fileType ?? "-"}
                </div>
              </div>

              <div className="flex flex-row w-full ">
                <div className="w-1/2 text-secondary-steelBlue-500 font-normal text-xs">
                  Download
                </div>
                <div className="w-1/2 text-xs text-black font-normal">
                  <button
                    className="flex justify-center items-center rounded-full h-9 w-9 bg-[#EBECEE] disabled:opacity-50"
                    onClick={onDownloadMedia}
                  >
                    <img src={DownloadIcon} />
                  </button>
                </div>
              </div>

              {list.length ? (
                <div className="flex flex-row items-center gap-5 mt-10">
                  <button
                    className="flex justify-center items-center rounded-full h-9 w-9 bg-[#EBECEE] disabled:opacity-50"
                    onClick={onLeftClick}
                    disabled={Number(currentSectionIndex) === 0}
                  >
                    <img src={LeftIcon} />
                  </button>
                  <p className="text-xs font-normal">
                    {Number(currentSectionIndex) >= 0 ? Number(currentSectionIndex) + 1 : "--"}
                    <span className="text-secondary-steelBlue-500">{` / ${list.length}`}</span>
                  </p>
                  <button
                    className="flex justify-center items-center rounded-full h-9 w-9 bg-[#EBECEE] disabled:opacity-50"
                    onClick={onRightClick}
                    disabled={Number(currentSectionIndex) === list.length - 1}
                  >
                    <img src={RightIcon} />
                  </button>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>
        </div>
      </DialogBox>
    </div>
  );
};

export default MediaGallery;
