import skipBackIcon from "assets/img/audioPlayer/ap_backward_skip.svg";
import micIcon from "assets/img/audioPlayer/ap_center_mic.svg";
import skipForwardIcon from "assets/img/audioPlayer/ap_forward_skip.svg";
import loopEnabledIcon from "assets/img/audioPlayer/ap_loop.svg";
import loopDisabledIcon from "assets/img/audioPlayer/ap_loop_disabled.svg";
import volumeMuteIcon from "assets/img/audioPlayer/ap_mute.svg";
import pauseIcon from "assets/img/audioPlayer/ap_pause.svg";
import playIcon from "assets/img/audioPlayer/ap_play.svg";
import volumeHighIcon from "assets/img/audioPlayer/ap_vol_high.svg";
import volumeMidIcon from "assets/img/audioPlayer/ap_vol_mid.svg";
import React, { BaseSyntheticEvent, useCallback, useEffect, useRef, useState } from "react";

interface IAudioPlayerProps {
  className?: string;
  mediaSource: string;
}

const AudioPlayer: React.FC<IAudioPlayerProps> = (props: IAudioPlayerProps) => {
  const { className, mediaSource } = props;

  /*-------------------State Variables----------------------*/
  const [currentTrack, setCurrentTrack] = useState<string>(mediaSource);
  const [isPlaying, setIsPlaying] = useState<boolean>(false);
  const [timeProgress, setTimeProgress] = useState<number>(0);
  const [duration, setDuration] = useState<number>(0);
  const [volume, setVolume] = useState<number>(100);
  const [loop, setLoop] = useState<boolean>(false);

  /*-----------------Component References--------------------*/
  const audioRef = useRef<HTMLAudioElement>(null);
  const progressBarRef = useRef<HTMLInputElement>(null);
  const playAnimationRef = useRef<number>();

  /*-----------------------------Hooks------------------------------- */
  const repeat = useCallback(() => {
    let currentTime = 0;
    if (audioRef.current && progressBarRef.current) {
      currentTime = audioRef.current.currentTime;
      setTimeProgress(currentTime);
      progressBarRef.current.value = "" + currentTime; //convert current time to string and assign

      if (duration) {
        progressBarRef.current.style.setProperty(
          "--range-progress",
          `${(parseInt(progressBarRef.current.value) / Math.round(duration)) * 100}%`
        );
      }
    }

    if (playAnimationRef.current && currentTime < duration) {
      // console.log("running");
      playAnimationRef.current = requestAnimationFrame(repeat);
    }
  }, [audioRef, duration, progressBarRef, setTimeProgress]);

  useEffect(() => {
    if (audioRef.current) {
      if (isPlaying) audioRef.current.play();
      else audioRef.current.pause();

      playAnimationRef.current = requestAnimationFrame(repeat);
    }
  }, [isPlaying, audioRef, repeat]);

  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.volume = volume / 100;
    }
  }, [volume, audioRef]);

  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.loop = loop;
    }
  }, [loop]);

  /* ------------------------Event Handlers---------------------------*/

  const onToggleLoop = () => {
    setLoop((prev) => !prev);
  };

  const onTogglePlayPause = () => {
    setIsPlaying((prev) => !prev);
  };

  const onLoadedMetadata = () => {
    if (audioRef.current && progressBarRef.current) {
      const seconds = audioRef.current.duration;
      setDuration(seconds);
      progressBarRef.current.max = "" + Math.round(seconds);
    }
  };

  const skipForward = () => {
    if (audioRef.current) audioRef.current.currentTime += 3;
  };

  const skipBackward = () => {
    if (audioRef.current) audioRef.current.currentTime -= 3;
  };

  const handleProgressChange = () => {
    if (audioRef.current && progressBarRef.current) {
      audioRef.current.currentTime = Number(progressBarRef.current.value);
    }
  };

  const handleVolumeChange = (e: BaseSyntheticEvent) => setVolume(e.target.value);

  /*-------------------------Helper Functions----------------------------*/

  const formatTime = (time: number) => {
    if (time && !isNaN(time)) {
      const minutes = Math.floor(time / 60);
      const formatMinutes = minutes < 10 ? `0${minutes}` : `${minutes}`;
      const seconds = Math.floor(time % 60);
      const formatSeconds = seconds < 10 ? `0${seconds}` : `${seconds}`;
      return `${formatMinutes}:${formatSeconds}`;
    }
    return "00:00";
  };

  const getVolIcon = () => {
    if (volume > 50) return volumeHighIcon;
    else if (volume > 0) return volumeMidIcon;
    else return volumeMuteIcon;
  };

  return (
    <>
      <div
        className={`audio-player-wrapper w-full h-full bg-secondary-steelBlue-300 rounded-lg ${
          className ?? ""
        }`}
      >
        {/* -----------------------audio player controls------------------- */}

        <div className="w-full h-full flex flex-col justify-end">
          <div className="mt-20 mb-16 flex items-center justify-center">
            <img src={micIcon} />
          </div>

          {/* display track */}
          <div>
            <audio ref={audioRef} src={currentTrack} onLoadedMetadata={onLoadedMetadata} />

            {/* <div className="audio-info">
              <div className="audio-image">
                {currentTrack.thumbnail ? (
                  <img src={currentTrack.thumbnail} alt="audio avatar" />
                ) : (
                  <div className="icon-wrapper">
                    <span className="audio-icon">
                      <img src={} alt="" />
                    </span>
                  </div>
                )}
              </div>
              <div className="text">
                <p className="title">{currentTrack.title}</p>
                <p>{currentTrack.author}</p>
              </div>
            </div> */}
          </div>

          <div className="w-full h-32 p-6 rounded-xl flex flex-col bg-secondary-gray-1000 justify-center">
            <div className="ap-progress-bar-wrapper flex gap-6 items-center">
              <span className="text-white">{formatTime(timeProgress)}</span>
              <input
                ref={progressBarRef}
                className="w-full"
                type="range"
                defaultValue={0}
                onChange={handleProgressChange}
              />
              <span className="text-white">{formatTime(duration)}</span>
            </div>

            <div className="ap-controls-wrapper flex items-center justify-center gap-6 relative">
              <button onClick={skipBackward}>
                <img src={skipBackIcon} alt="skip-back" />
              </button>

              <button
                className="w-16 h-16 bg-white rounded-full flex items-center justify-center cursor-pointer"
                onClick={onTogglePlayPause}
              >
                <img id="ap-play-icon" src={isPlaying ? pauseIcon : playIcon} alt="play/pause" />
              </button>

              <button onClick={skipForward}>
                <img src={skipForwardIcon} alt="skip-forward" />
              </button>

              <div className="absolute left-0 bottom-0 top-0 flex items-center justify-center">
                <button onClick={onToggleLoop}>
                  <img src={loop ? loopEnabledIcon : loopDisabledIcon} alt="loop" />
                </button>
              </div>

              <div className="absolute right-0 bottom-0 top-0 flex items-center justify-center">
                <button>
                  <img src={getVolIcon()} alt="volume" />
                </button>

                <input
                  className="w-22"
                  type="range"
                  min={0}
                  max={100}
                  value={volume}
                  onChange={handleVolumeChange}
                  style={{
                    background: `linear-gradient(to right, #f50 ${volume}%, #ccc ${volume}%)`,
                  }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default AudioPlayer;
