import React, { useState } from "react";
import { Document, Page } from "react-pdf";

import { pdfjs } from "react-pdf";

//For React-PDF to work, PDF.js worker needs to be provided
pdfjs.GlobalWorkerOptions.workerSrc = new URL(
  "pdfjs-dist/build/pdf.worker.min.js",
  import.meta.url
).toString();

interface IPdfCustomProps {
  className?: string;
  source: string;
}

const PdfCustom: React.FC<IPdfCustomProps> = (props: IPdfCustomProps) => {
  const { className, source } = props;

  const [numPages, setNumPages] = useState<number>();
  const [pageNumber, setPageNumber] = useState<number>(1);

  const onDocumentLoadSuccess = ({ numPages }: { numPages: number }): void => {
    setNumPages(numPages);
  };

  return (
    <div
      className={`w-full h-full flex border rounded-xl  items-center justify-center text-xs ${
        className ?? ""
      }`}
    >
      <Document
        className={`pdf-card`}
        file={{
          url: source,
        }}
        loading={
          <div className="w-full h-full flex items-center justify-center text-xxs">
            Loading PDF...
          </div>
        }
        onLoadSuccess={onDocumentLoadSuccess}
        error={
          <div className="w-full h-full flex items-center justify-center text-xxs">
            Failed to load PDF
          </div>
        }
      >
        <Page
          className={`pdf-card-page`}
          pageNumber={pageNumber}
          renderAnnotationLayer={false}
          renderTextLayer={false}
          loading={
            <div className="w-full h-full flex items-center justify-center text-xxs">
              Loading Page...
            </div>
          }
        />
      </Document>
    </div>
  );
};

// Memoization
export default React.memo(PdfCustom);

